import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { JobTitleComponent } from './job-title/job-title.component';
import { DisciplineComponent } from './discipline/discipline.component';
import { LegalComponent } from './legal/legal.component';
import { ResponsibilityComponent } from './responsibility/responsibility.component';
import { StackComponent } from './stack/stack.component';

const routes: Routes = [
  { path: 'job-title', component: JobTitleComponent },
  { path: 'discipline', component: DisciplineComponent },
  { path: 'legal', component: LegalComponent },
  { path: 'responsibility', component: ResponsibilityComponent },
  { path: 'stack', component: StackComponent },
  { path: '', redirectTo: '/job-title', pathMatch: 'full' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
