import { Component, OnInit, ViewChild, ElementRef, AfterViewInit } from '@angular/core';
import { Router } from '@angular/router';
import { JobService } from '../job.service';

@Component({
  selector: 'app-stack',
  templateUrl: './stack.component.html',
  styleUrls: ['./stack.component.sass']
})
export class StackComponent implements OnInit, AfterViewInit {

  public stack: Array<string> = []
  @ViewChild('tags') tagsDiv: ElementRef

  constructor(private jobService: JobService, private router: Router) { }

  ngOnInit () {
    const savedStack: Array<string> = this.jobService.getStack()
    this.stack = [...savedStack]
  }

  ngAfterViewInit () {
    this.tagsDiv.nativeElement.scrollTop = this.tagsDiv.nativeElement.scrollHeight
  }

  back (): void {
    this.router.navigateByUrl('responsibility')
  }

  addToStack (skill: string): void {
    if (skill) {
      this.stack.push(skill)
      this.jobService.addToStack(skill)
      this.tagsDiv.nativeElement.scrollTop = this.tagsDiv.nativeElement.scrollHeight
    }
  }

  removeFromStack (i: number): void {
    this.stack.splice(i, 1)
    this.jobService.removeFromStack(i)
  }

}
