import { Component, OnInit } from '@angular/core';
import { JobService } from '../job.service';
import { Router } from '@angular/router';
import { Responsibility } from '../responsibility';

@Component({
  selector: 'app-responsibility',
  templateUrl: './responsibility.component.html',
  styleUrls: ['./responsibility.component.sass']
})
export class ResponsibilityComponent implements OnInit {

  public responsibility: Responsibility = new Responsibility()

  constructor(private jobService: JobService, private router: Router) { }

  ngOnInit() {
    const savedResponsibilityValues: {
      seniority: string,
      teamSize: string,
      companySize: string
    } = this.jobService.responsibility.getResponsibility()
    this.responsibility.setSeniority(savedResponsibilityValues.seniority)
    this.responsibility.setTeamSize(savedResponsibilityValues.teamSize)
    this.responsibility.setCompanySize(savedResponsibilityValues.companySize)
  }

  back (): void {
    this.router.navigateByUrl('legal')
  }

  setSeniority (event: Event): void {
    const seniority = (<HTMLSelectElement>event.target).value
    this.responsibility.setSeniority(seniority)
    this.jobService.responsibility.setSeniority(seniority)
  }

  setTeamSize (event: Event): void {
    const teamSize = (<HTMLSelectElement>event.target).value
    this.responsibility.setTeamSize(teamSize)
    this.jobService.responsibility.setTeamSize(teamSize)
  }

  setCompanySize (event: Event): void {
    const companySize = (<HTMLSelectElement>event.target).value
    this.responsibility.setCompanySize(companySize)
    this.jobService.responsibility.setCompanySize(companySize)
  }

  submitResponsibility (): void {
    this.router.navigateByUrl('/stack')
  }

}
