export class Legal {

  private location: string
  private isManagerRole: boolean
  private hasRepetitiveMotions: boolean
  private isNoisyEnvironment: boolean
  private hasHeavyLifting: boolean

  public getLocation (): string {
    return this.location
  }

  public getIsManagerRole (): boolean {
    return this.isManagerRole
  }

  public getHasRepetitiveMotions (): boolean {
    return this.hasRepetitiveMotions
  }

  public getIsNoisyEnvironment (): boolean {
    return this.isNoisyEnvironment
  }

  public getHasHeavyLifting (): boolean {
    return this.hasHeavyLifting
  }

  public getLegal (): {
    location: string,
    isManagerRole: boolean,
    hasRepetitiveMotions: boolean,
    isNoisyEnvironment: boolean,
    hasHeavyLifting: boolean
  } {
    return {
      location: this.location,
      isManagerRole: this.isManagerRole,
      hasRepetitiveMotions: this.hasRepetitiveMotions,
      isNoisyEnvironment: this.isNoisyEnvironment,
      hasHeavyLifting: this.hasHeavyLifting
    }
  }

  public setLocation (location: string): void {
    this.location = location
  }

  public setIsManagerRole (isManagerRole: boolean): void {
    this.isManagerRole = isManagerRole
  }

  public setHasRepetitiveMotions (hasRepetitiveMotions: boolean): void {
    this.hasRepetitiveMotions = hasRepetitiveMotions
  }

  public setIsNoisyEnvironment (isNoisyEnvironment: boolean): void {
    this.isNoisyEnvironment = isNoisyEnvironment
  }

  public setHasHeavyLifting (hasHeavyLifting: boolean): void {
    this.hasHeavyLifting = hasHeavyLifting
  }

}
