export class Discipline {

  private kingdom: string
  private phylum: string
  private disciplineClass: string
  private order: string

  public getKingdom (): string {
    return this.kingdom
  }

  public getPhylum (): string {
    return this.phylum
  }

  public getDisciplineClass (): string {
    return this.disciplineClass
  }

  public getOrder (): string {
    return this.order
  }

  public getDiscipline (): {
    kingdom: string,
    phylum: string,
    disciplineClass: string,
    order: string
  } {
    return {
      kingdom: this.kingdom,
      phylum: this.phylum,
      disciplineClass: this.disciplineClass,
      order: this.order
    }
  }

  public setKingdom (kingdom: string): void {
    this.kingdom = kingdom
  }

  public setPhylum (phylum: string): void {
    this.phylum = phylum
  }

  public setDisciplineClass (disciplineClass: string): void {
    this.disciplineClass = disciplineClass
  }

  public setOrder (order: string): void {
    this.order = order
  }

}
