export class Responsibility {
  private seniority: string
  private companySize: string
  private teamSize: string

  public getSeniority (): string {
    return this.seniority
  }

  public getCompanySize (): string {
    return this.companySize
  }

  public getTeamSize (): string {
    return this.teamSize
  }

  public getResponsibility (): { seniority: string, companySize: string, teamSize: string } {
    return { seniority: this.seniority, companySize: this.companySize, teamSize: this.teamSize }
  }

  public setSeniority (seniority: string): void {
    this.seniority = seniority
  }

  public setCompanySize (companySize: string): void {
    this.companySize = companySize
  }

  public setTeamSize (teamSize: string): void {
    this.teamSize = teamSize
  }

  public setResponsibility (responsibility: { seniority: string, companySize: string, teamSize: string }): void {
    ({ seniority: this.seniority, companySize: this.companySize, teamSize: this.teamSize } = responsibility)
  }
}
